<template>
    <div class="more">
    <ul>
        <li>
            <p>热门分类</p>
            <dl>
                <dd class="hotList" v-for="(item,index) in hotList" @click="pubSelect(item)" :class="{'checkActive':item.active}"><span>{{item.it}}</span> <i></i> </dd>
            </dl>
        </li>
        <li>
            <p>选择车型</p>
            <dl>
                <dd class="carsarr" v-for="(item,index) in carsarr" @click="pubSelect(item)" :class="{'checkActive':item.active}"> <span>{{item.it}}</span> <i></i> </dd>
            </dl>
        </li>
        <li style="height:2.2rem;">
            <p>车龄 <span>(年)</span></p>
            <div style="width:90%;margin:1rem auto 0;" class="setAges">
                <input type="hidden" class="slider-ages" value="123" />
            </div>
        </li>
        <li style="height:2.2rem;">
            <p>里程 <span>(万公里)</span> </p>
            <div style="width:90%;margin:1rem auto 0;" class="setMileages">
                <input type="hidden" class="slider-mileages" value="123" />
            </div>
        </li>
        <li style="height:2.2rem;">
            <p>排量 <span>(升)</span> </p>
            <div style="width:90%;margin:1rem auto 0;" class="setDisplacements">
                <input type="hidden" class="slider-displacements" value="123"/>
            </div>
        </li>
        <li>
            <p>变速箱</p>
            <dl>
                <dd class="biansuarr" v-for="(item,index) in biansuxiangarr" @click="pubSelect(item)" :class="{'checkActive':item.active}"> <span>{{item.it}}</span> <i></i> </dd>
            </dl>
        </li>
        <li>
            <p>排放标准</p>
            <dl>
                <dd class="paifangarr" v-for="(item,index) in paifangarr" @click="pubSelect(item)" :class="{'checkActive':item.active}"> <span>{{item.it}}</span> <i></i> </dd>
            </dl>
        </li>
        <li>
            <p>座位</p>
            <dl>
                <dd class="zuoweiarr" v-for="(item,index) in zuoweiarr" @click="pubSelect(item)" :class="{'checkActive':item.active}">
                    <span>
                        <template v-if="item.it == 'gt5'">5座以上</template>
                        <template v-else-if="item.it == '2'">2座</template>
                        <template v-else-if="item.it == '5'">5座</template>
                    </span>
                    <i></i>
                </dd>
            </dl>
        </li>
        <li>
            <p>驱动</p>
            <dl>
                <dd class="qudongarr" v-for="(item,index) in qudongarr" @click="pubSelect(item)" :class="{'checkActive':item.active}"><span>{{item.it}}</span><i></i></dd>
            </dl>
        </li>
        <li>
            <p>颜色</p>
            <dl>
                <dd class="colorarr" v-for="(item,index) in colorarr" @click="pubSelect(item)" :class="{'checkActive':item.active}">
                    <template v-if="item.it == '黑色'"><strong class="dot black"></strong>
                    </template>
                    <template v-if="item.it == '白色'"><strong class="dot white"></strong>
                    </template>
                    <template v-if="item.it == '灰色'"><strong class="dot gray"></strong>
                    </template>
                    <template v-if="item.it == '红色'"><strong class="dot red"></strong>
                    </template>
                    <template v-if="item.it == '蓝色'"><strong class="dot blue"></strong>
                    </template>
                    <template v-if="item.it == '金色'"><strong class="dot gold"></strong>
                    </template>
                    <template v-if="item.it == '银色'"><strong class="dot silver"></strong>
                    </template>
                    <template v-if="item.it == '黄色'"><strong class="dot yellow"></strong>
                    </template>
                    <template v-if="item.it == '紫色'"><strong class="dot purple"></strong>
                    </template>
                    <template v-if="item.it == '橙色'"><strong class="dot orange"></strong>
                    </template>
                    <template v-if="item.it == '棕色'"><strong class="dot brown"></strong>
                    </template>
                    <template v-if="item.it == '其它'"><strong class="dot other"> </strong>
                    </template>
                    <span class="cclore" style="margin-right:.2rem;">{{item.it}}</span>
                    <i></i>
                </dd>
            </dl>
        </li>
    </ul>
    <div class="closeShow clearfix">  <span id="close" @click="hideMore">关闭</span>
    <span @click="moreClickEvent">查看</span>

    </div>
</div>
</template>

<script>
import Vue from 'vue';
export
default {
    data() {
        return {
            hotList: [
                {
                    it: "准新车"
                },
                {
                    it: "练手车"
                }
            ],
            carsarr: [],
            biansuxiangarr: [],
            paifangarr: [],
            zuoweiarr: [],
            qudongarr: [],
            colorarr: [],
            pailiangarr: [],
            agesarr: [],
            lowAges: '0',
            highAges: '11',
            agesBooleans:false,
            lowMileages:'',
            highMileages:'',
            mileagesBooleans:false,
            lowDisplacements:'',
            highDisplacements:'',
            displacementsBooleans:false,
        }
    },
    props: {
        pTocMore: {}
    },
    created() {
        var _self = this;
        var heigh = $('.setAges .high').eq(1).html();
        _self.highAges = heigh;
        // if(heigh == '11'){
        //     $('.setAges .high').eq(1).html('不限');
        // };

        // 车龄
        setTimeout(function () {
            $('.slider-ages').jRange({
                from: 0,
                to: 11,
                step: 1,
                showLabels: true,
                isRange: true,
                ondragend: ondragend
            }, 0);

            var low = $('.setAges .low').eq(1).html(),
                heigh = $('.setAges .high').eq(1).html();

                _self.highAges = heigh;

                // if(heigh == '11'){
                    // $('.setAges .high').eq(1).html('不限')
                // }

            function ondragend() {
                var low = $('.setAges .low').eq(1).html(),
                    heigh = $('.setAges .high').eq(1).html();
                    // if(heigh == '11'){
                    //     $('.setAges .high').eq(1).html('不限')
                    // }

                _self.lowAges = low;
                _self.highAges = heigh;

                if(_self.lowAges!= '0' || _self.highAges!= '11'){
                    _self.agesBooleans = true;
                }else{
                    _self.agesBooleans = false;
                }

            }
        },10)

        //里程
        setTimeout(function () {
            $('.slider-mileages').jRange({
                from: 0,
                to: 16,
                step: 1,
                showLabels: true,
                isRange: true,
                ondragend: ondragend
            }, 0);

            var low = $('.setMileages .low').eq(1).html(),
                heigh = $('.setMileages .high').eq(1).html();

                _self.highMileages = heigh;

            // if(heigh == '16'){
                // $('.setMileages .high').eq(1).html('不限')
            // }

            function ondragend() {
                var low = $('.setMileages .low').eq(1).html(),
                    heigh = $('.setMileages .high').eq(1).html();

                // if(heigh == '16'){
                //     $('.setMileages .high').eq(1).html('不限')
                // }

                _self.lowMileages = low;
                _self.highMileages = heigh;

                if(_self.lowAges!= '0' || _self.highAges!= '16'){
                    _self.mileagesBooleans = true;
                }else{
                    _self.mileagesBooleans = false;
                }
            }
        },10)

        //排量
        setTimeout(function () {
            $('.slider-displacements').jRange({
                from: 0,
                to: 5.5,
                step: 0.5,
                showLabels: true,
                isRange: true,
                ondragend: ondragend,
                bocai:1
            },0);

            var low = $('.setDisplacements .low').eq(1).html(),
                heigh = $('.setDisplacements .high').eq(1).html();

                _self.highDisplacements = heigh;

            // if(heigh == '5.5'){
                // $('.setDisplacements .high').eq(1).html('不限')
            // }

            function ondragend() {
                var low = $('.setDisplacements .low').eq(1).html(),
                    heigh = $('.setDisplacements .high').eq(1).html();
                // if(heigh == '5.5'){
                //     $('.setDisplacements .high').eq(1).html('不限')
                // }

                _self.lowDisplacements = low;
                _self.highDisplacements = heigh;

                if(_self.lowDisplacements!= '0.0' || _self.highDisplacements!= '5.5'){
                    _self.displacementsBooleans = true;
                }else{
                    _self.displacementsBooleans = false;
                }
            }
        },10)

    },
    mounted() {
        window.addEventListener('scroll', this.showHtml);
        var _this = this;
        function setJson(a, b) {
            _this[a] = _this.pTocMore[b];
            var arr = [];
            _this[a].forEach(function (index) {
                arr.push({
                    it: index
                });
            })
            _this[a] = arr;
        }
        setJson('carsarr', 'levels');
        setJson('biansuxiangarr', 'greaboxs');
        setJson('paifangarr', 'emissionStandards');
        setJson('zuoweiarr', 'seatings');
        setJson('qudongarr', 'drives');
        setJson('colorarr', 'colors');
    },
    methods: {
        showHtml() {
            // if(this.highAges == '11'){
            //     $('.setAges .high').eq(1).html('不限');
            // }
            // if(this.highMileages == '16'){
            //     $('.setMileages .high').eq(1).html('不限')
            // }
            // if(this.highDisplacements == '5.5'){
            //     $('.setDisplacements .high').eq(1).html('不限')
            // }
        },
        hideMore() {
            var False = false;
            this.$emit('hideMore', False);
        },
        pubSelect(item) {

            if(item.active == undefined) {
             Vue.set(item, "active", false);
            }
            item.active = !item.active;

        },
        moreClickEvent() {
            var city = window.localStorage.getItem("city"),
                reg = new RegExp('"', "g");
            city = city.replace(reg, "");
            city = Number(city);
            var storage = window.localStorage;
            if (storage.getItem("user")) {
                var token = JSON.parse(storage.getItem("user")).token;
            }

            var actives = $('.checkActive');
            var moreCheckArr = {};
            var moreShowCheck = [];

            //热门分类
            var hotListArr = $('.hotList');
            hotListArr.each(function(i){
                if(hotListArr.eq(i).hasClass('checkActive')){
                    var _thisspan = $(this).find('span').eq(0);

                    moreShowCheck.push({
                        name: _thisspan.html()
                    });

                    if(_thisspan.html() == '准新车'){
                        moreCheckArr.isPracticeCar = "N";
                        moreCheckArr.isNearlyNewCar = "Y";
                    }else if(_thisspan.html() == '练手车'){
                        moreCheckArr.isPracticeCar = "Y";
                        moreCheckArr.isNearlyNewCar = "N";
                    }

                    if(moreShowCheck.length == 2){
                        moreCheckArr.isPracticeCar = "Y";
                        moreCheckArr.isNearlyNewCar = "Y";
                    }
                }
            })

            //选择车型
            var carsarr = $('.carsarr');
            var carsparameter = [];
            carsarr.each(function(i){
                if(carsarr.eq(i).hasClass('checkActive')){
                    var _thisspan = $(this).find('span').eq(0);
                    moreShowCheck.push({
                        name: _thisspan.html()
                    });
                    carsparameter.push(_thisspan.html());
                }
            })
            moreCheckArr.typeLevels = carsparameter.join(',');


            //选择车龄
            if(this.agesBooleans == true){
                let min_coty = "";
                let max_coty = "";
                let htmlAges = '';
                if (this.lowAges == '0' && this.highAges !== '不限') {
                    max_coty = this.highAges;
                    htmlAges = this.highAges + '年以下';
                } else if (this.lowAges != '0' && this.highAges == '不限') {
                    min_coty = this.lowAges;
                    htmlAges = this.lowAges + '年以上';
                } else if (this.lowAges != '0' && this.highAges != '不限') {
                    min_coty = this.lowAges;
                    max_coty = this.highAges;
                    htmlAges = this.lowAges + '-' + this.highAges + '年';
                }
                moreCheckArr.ageRange = [min_coty,max_coty].join(',');
                moreShowCheck.push({
                    name: htmlAges
                });
            }

            //公里数
            if(this.mileagesBooleans == true){
                let min_mileage = '';
                let max_mileage = '';
                let htmlMileages = '';

                if (this.lowMileages == '0' && this.highMileages !== '不限') {
                    max_mileage = this.highMileages;
                    htmlMileages = this.highMileages + '万公里以下';
                } else if (this.lowMileages != '0' && this.highMileages == '不限') {
                    min_mileage = this.lowMileages;
                    htmlMileages = this.lowMileages + '万公里以上';
                } else if (this.lowMileages != '0' && this.highMileages != '不限') {
                    min_mileage = this.lowMileages;
                    max_mileage = this.highMileages;
                    htmlMileages = this.lowMileages + '-' + this.highMileages + '万公里';
                }
                moreCheckArr.mileageRange = [min_mileage,max_mileage].join(',');
                moreShowCheck.push({
                    name: htmlMileages
                });
            }

            //排量
            if(this.displacementsBooleans == true){
                let min_displacement = '';
                let max_displacement = '';
                let htmlDisplacement = '';

                if (this.lowDisplacements == '0.0' && this.highDisplacements !== '不限') {
                    max_displacement = this.highDisplacements;
                    htmlDisplacement = this.highDisplacements + '升以下';
                } else if (this.lowDisplacements != '0.0' && this.highDisplacements == '不限') {
                    min_displacement = this.lowDisplacements;
                    htmlDisplacement = this.lowDisplacements + '升以上';
                } else if (this.lowDisplacements != '0.0' && this.highDisplacements != '不限') {
                    min_displacement = this.lowDisplacements;
                    max_displacement = this.highDisplacements;
                    htmlDisplacement = this.lowDisplacements + '-' + this.highDisplacements + '升';
                }
                moreCheckArr.displacementRange = [min_displacement,max_displacement].join(',') ;
                moreShowCheck.push({
                    name: htmlDisplacement
                });
            }

            //变速箱
            var biansuarr = $('.biansuarr');
            var biansuparameter = [];
            biansuarr.each(function(i){
                if(biansuarr.eq(i).hasClass('checkActive')){
                    var _thisspan = $(this).find('span').eq(0);
                    moreShowCheck.push({
                        name: _thisspan.html()
                    });
                    biansuparameter.push(_thisspan.html());
                }
            })
            moreCheckArr.gearboxes = biansuparameter.join(',');

            //排放标准
            var paifangarr = $('.paifangarr');
            var paifangparameter = [];
            paifangarr.each(function(i){
                if(paifangarr.eq(i).hasClass('checkActive')){
                    var _thisspan = $(this).find('span').eq(0);
                    moreShowCheck.push({
                        name: _thisspan.html()
                    });
                    paifangparameter.push(_thisspan.html());
                }
            })
            moreCheckArr.emissionStandards =  paifangparameter.join(',');

            //座位
            var zuoweiarr = $('.zuoweiarr');
            var zuiweiparameter = [];
            zuoweiarr.each(function(i){
                if(zuoweiarr.eq(i).hasClass('checkActive')){
                    var _thisspan = $(this).find('span').eq(0).html();
                    var _thisa = $(this).find('span').eq(0).html().substr(0, 1);
                    moreShowCheck.push({
                        name: _thisspan
                    });
                    zuiweiparameter.push(_thisa);

                    if (_thisspan == '5座以上') {
                        moreCheckArr.seats = 'gt5';
                    } else {
                        moreCheckArr.seats = zuiweiparameter.join(',');
                    }
                }
            })

            console.log('moreCheckArr.seats',moreCheckArr.seats);

            //驱动
            var qudongarr = $('.qudongarr');
            var qudongparameter = [];
            qudongarr.each(function(i){
                if(qudongarr.eq(i).hasClass('checkActive')){
                    var _thisspan = $(this).find('span').eq(0);
                    moreShowCheck.push({
                        name: _thisspan.html()
                    });
                    qudongparameter.push(_thisspan.html());
                }
            })
            moreCheckArr.drives = qudongparameter.join(',');

            //颜色
            var colorarr = $('.colorarr');
            var colorparameter = [];
            colorarr.each(function(i){
                if(colorarr.eq(i).hasClass('checkActive')){
                    var _thisspan = $(this).find('span').eq(0);
                    moreShowCheck.push({
                        name: _thisspan.html()
                    });
                    colorparameter.push(_thisspan.html());
                }
            })
            moreCheckArr.colors = colorparameter.join(',');
            moreCheckArr.token = token;
            moreCheckArr.city = city;
            var False = false;
            this.$emit('moreClickEvent',moreCheckArr,moreShowCheck,False);
        }
    }
}
</script>


<style lang="scss">
.slider-container {
    width: 6rem!important;    
}
.pointer-label.high {
    left: 5.5rem!important;
}
.more {
    width: 100%;
    background: #eeeeee;
    position: absolute;
    left:0;
    top:.7rem;
    z-index: 2;
    .on {
        color: red;
    }
    ul {
        background: white;
        li {
            border-bottom: 10px solid #eee;
            p {
                font-size: .28rem;
                color: #000;
                margin: 0 .14rem .28rem .4rem;
                padding-top: .28rem;
                span {
                    font-size: .28rem;
                    color: #666;
                }
            }
            dl {
                display: flex;
                flex-wrap:wrap;
                dd {
                    text-align:center;
                    width: 2rem;
                    height: .6rem;
                    line-height: .6rem;
                    border:1px solid #f0f0f0;
                    border-radius: .1rem;
                    font-size: .26rem;
                    margin-bottom: .24rem;
                    margin-left: 5%;
                    color: #666;
                    box-shadow: 0 0 5px #f0f0f0;
                    display: flex;
                    justify-content:space-between;
                    span{
                        margin-left: .3rem;
                    }
                    i{
                        width: .26rem;
                        height: .16rem;
                        background: url('./right.png') no-repeat;
                        background-size: cover;
                        margin-top:.22rem;
                        margin-right: .18rem;
                    }
                }
                dd.checkActive {
                    border-color: #F6A32B;
                    color: #F6A32B;
                    i{
                        width: .26rem;
                        height: .16rem;
                        background: url('./rightActive.png') no-repeat;
                        background-size: cover;
                        margin-top:.2rem;
                        margin-right: .18rem;
                    }
                }
            }
        }
        li:last-child {
            margin-bottom: .8rem;
        }
    }
    .colorarr {
        display: flex;
        span:first-child {
            margin-left: .64rem;
        }
        .dot {
            margin-left: .3rem;
            margin-top: .2rem;
            display: block;
            width: .2rem;
            height: .2rem;
            border-radius: 50%;
        }
        .dot.black {
            background: #000;
        }
        .dot.white {
            background: #fff;
            border:1px solid #d4d4d4;
        }
        .dot.gray {
            background: gray;
        }
        .dot.red {
            background: red;
        }
        .dot.blue {
            background: blue;
        }
        .dot.gold {
            background: gold;
        }
        .dot.silver {
            background: silver;
        }
        .dot.yellow {
            background: yellow;
        }
        .dot.purple {
            background: purple;
        }
        .dot.orange {
            background: orange;
        }
        .dot.brown {
            background: brown;
        }
        .dot.other {
            background: url('./other.jpg') no-repeat;
            background-size: cover;
        }
    }
    .closeShow {
        width: 100%;
        background: white;
        position: fixed;
        bottom: 0;
        padding:.36rem 0;
        max-width:750px;
        z-index:3;
        display: inline-flex;
        justify-content:space-around;
        span {
            width:3rem;
            text-align:center;
            line-height:.7rem;
            height: .7rem;
            font-size:.28rem;
            color:#666;
            border:1px solid #f0f0f0;
            border-radius: 20px;
            box-shadow: 0 0 4px #f0f0f0;
        }
        span:last-child {
            border:none;
            background: #ffb700;
            color:#fff;
            box-shadow: 0 0 4px #ffb700;
        }
    }
}
.setAges {
}
</style>



